.payment-modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.7);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 999;
  }
  
  .payment-modal {
    background: #1e1e1e;
    padding: 2rem;
    border-radius: 10px;
    max-width: 400px;
    width: 90%;
    color: white;
    position: relative;
    box-shadow: 0 0 10px #000;
  }
  
  .payment-form {
    display: flex;
    flex-direction: column;
  }
  
  .payment-form input {
    padding: 10px;
    margin-bottom: 15px;
    background-color: #2c2c2c;
    border: none;
    border-radius: 5px;
    color: white;
  }
  
  .payment-row {
    display: flex;
    gap: 10px;
  }
  
  .payment-row input {
    flex: 1;
  }
  
  .payment-form button {
    background-color: #4bb869;
    padding: 10px;
    border: none;
    border-radius: 5px;
    font-weight: bold;
    cursor: pointer;
  }
  
  .close-btn {
    position: absolute;
    top: 10px;
    right: 15px;
    font-size: 1.2rem;
    background: none;
    border: none;
    color: white;
    cursor: pointer;
  }
  